import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { authViaTonConnect, createProServiceInvoice, createRecipient, getBackupState, getProServiceTiers, getProState, logoutTonConsole, setBackupState, startProServiceTrial, waitProServiceInvoice } from '@tonkeeper/core/dist/service/proService';
import { getWalletState } from '@tonkeeper/core/dist/service/wallet/storeService';
import { useMemo } from 'react';
import { useAppContext, useWalletContext } from '../hooks/appContext';
import { useAppSdk } from '../hooks/appSdk';
import { useTranslation } from '../hooks/translation';
import { QueryKey } from '../libs/queryKey';
import { signTonConnectOver } from './mnemonic';
import { useCheckTouchId } from './password';
export const useProBackupState = () => {
    const sdk = useAppSdk();
    return useQuery([QueryKey.proBackup], () => getBackupState(sdk.storage), { keepPreviousData: true });
};
export const useProState = () => {
    const wallet = useWalletContext();
    const sdk = useAppSdk();
    const client = useQueryClient();
    return useQuery([QueryKey.pro], async () => {
        const state = await getProState(sdk.storage, wallet);
        await setBackupState(sdk.storage, state.subscription);
        await client.invalidateQueries([QueryKey.proBackup]);
        return state;
    });
};
export const useSelectWalletMutation = () => {
    const sdk = useAppSdk();
    const client = useQueryClient();
    const { api } = useAppContext();
    const { t } = useTranslation();
    const { mutateAsync: checkTouchId } = useCheckTouchId();
    return useMutation(async (publicKey) => {
        const state = await getWalletState(sdk.storage, publicKey);
        if (!state) {
            throw new Error('Missing wallet state');
        }
        await authViaTonConnect(api, state, signTonConnectOver(sdk, publicKey, t, checkTouchId));
        await client.invalidateQueries([QueryKey.pro]);
    });
};
export const useProLogout = () => {
    const client = useQueryClient();
    return useMutation(async () => {
        await logoutTonConsole();
        await client.invalidateQueries([QueryKey.pro]);
    });
};
export const useProPlans = (promoCode) => {
    const wallet = useWalletContext();
    const all = useQuery([QueryKey.pro, 'plans', wallet.lang], () => getProServiceTiers(wallet.lang));
    const promo = useQuery([QueryKey.pro, 'promo', wallet.lang, promoCode], () => getProServiceTiers(wallet.lang, promoCode !== '' ? promoCode : undefined), { enabled: promoCode !== '' });
    return useMemo(() => {
        if (!promo.data) {
            return [all.data, undefined];
        }
        else {
            return [promo.data, promoCode];
        }
    }, [all.data, promo.data]);
};
export const useCreateInvoiceMutation = () => {
    const sdk = useAppSdk();
    const { api } = useAppContext();
    return useMutation(async (data) => {
        if (data.tierId === null) {
            throw new Error('missing tier');
        }
        const wallet = await getWalletState(sdk.storage, data.state.wallet.publicKey);
        if (!wallet) {
            throw new Error('Missing wallet');
        }
        const invoice = await createProServiceInvoice(data.tierId, data.promoCode);
        const [recipient, assetAmount] = await createRecipient(api, invoice);
        return {
            invoice,
            wallet,
            recipient,
            assetAmount
        };
    });
};
export const useWaitInvoiceMutation = () => {
    const client = useQueryClient();
    return useMutation(async (data) => {
        await waitProServiceInvoice(data.invoice);
        await client.invalidateQueries([QueryKey.pro]);
    });
};
export const useActivateTrialMutation = () => {
    const client = useQueryClient();
    const ctx = useAppContext();
    const { i18n: { language } } = useTranslation();
    return useMutation(async () => {
        await startProServiceTrial(ctx.env.tgAuthBotId, language);
        await client.invalidateQueries([QueryKey.pro]);
    });
};
