export async function getDashboardColumns(lang) {
    if (!Object.values(Lang).includes(lang)) {
        lang = Lang.EN;
    }
    const result = await ProServiceService.proServiceDashboardColumns(lang);
    return result.items.map(item => ({
        id: item.id,
        name: item.name,
        type: item.column_type,
        defaultIsChecked: item.checked_default,
        onlyPro: item.only_pro
    }));
}
export async function getDashboardData(query, options) {
    let lang = Lang.EN;
    if (Object.values(Lang).includes(options?.lang)) {
        lang = options?.lang;
    }
    let currency = FiatCurrenciesGenerated.USD;
    if (Object.values(FiatCurrenciesGenerated).includes(options?.currency)) {
        currency = options?.currency;
    }
    const result = await ProServiceService.proServiceDashboardData(lang, currency, query);
    return result.items.map(row => row.map(mapDtoCellToCell));
}
function mapDtoCellToCell(dtoCell) {
    switch (dtoCell.type) {
        case ProServiceDashboardColumnType.STRING: {
            const cell = dtoCell;
            return {
                columnId: cell.column_id,
                type: 'string',
                value: cell.value
            };
        }
        case ProServiceDashboardColumnType.ADDRESS: {
            const cell = dtoCell;
            return {
                columnId: cell.column_id,
                type: 'address',
                raw: cell.raw
            };
        }
        case ProServiceDashboardColumnType.NUMERIC_CRYPTO: {
            const cell = dtoCell;
            return {
                columnId: cell.column_id,
                type: 'numeric_crypto',
                value: new BigNumber(cell.value),
                decimals: cell.decimals,
                symbol: cell.symbol
            };
        }
        case ProServiceDashboardColumnType.NUMERIC_FIAT: {
            const cell = dtoCell;
            return {
                columnId: cell.column_id,
                type: 'numeric_fiat',
                value: new BigNumber(cell.value),
                fiat: cell.fiat
            };
        }
        default:
            throw new Error('Unsupported cell type');
    }
}
